.filter-container {
  h2 {
    text-transform: capitalize;
    font-family: "Roboto-Medium";
    font-size: 20px;
    border-bottom: 0.5px solid #919294;
    padding-bottom: 10px;
  }
  .filter-box {
    h2 {
      margin-bottom: 20px;
    }

    .option-box {
      display: flex;
      flex-direction: row-reverse;
      align-items: center;
      margin-bottom: 10px;
      h3 {
        margin-right: auto;
        margin-left: 20px;

        body.ar & {
          margin-right: 20px;
          margin-left: auto;
        }

        color: #919294;
        text-transform: capitalize;
        font-size: 20px;
        font-family: "Roboto-Regular";
      }
      label {
        margin-bottom: 0;
        margin-left: 10px;
        cursor: pointer;
        body.ar & {
          margin-left: unset;
          margin-right: 10px;
        }
        position: relative;
        margin-bottom: 6px;
        color: #919294;
        i {
          font-size: 25px;
          transition: 0.3s;
          opacity: 1;
          backface-visibility: hidden;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
        i.fa-check-square {
          opacity: 0;
          transition: 0.5s;
        }
      }
      input {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        &:checked ~ label i {
          opacity: 0;
        }
        &:checked ~ label i.fa-check-square {
          opacity: 1;
        }
      }
    }
  }
}
